<script>
import VueCoreImageUpload from 'vue-core-image-upload';
import JLApiUtils from '../../utils/JLApiUtils.js';
export default {
  data() {
    return {
      planStatus:0,
      elform1:{
        wenliName:'',
        area:'0',
        times:'1'
      },
      elform2:{
        wenliName:'',
        area:'0',
        times:'1'
      },
      innerVisible2:false,
      innerVisible1:false,
      urgencyDisable: false,
      changeUrgency:{
        urgency :'0'
      },
      urlId:'',
      qrCode:'',
      imageUrl: '',
      contr_loading: false,
      base_loading: false,
      exform: {
        addUserId: '',
        addUserName: '',
        orderId: '',
        qiKong: '',
        queKou: '',
        lieFeng: '',
        hanBa: '',
        dianHuoHua: '',
        youShiSi: '',
        heMoBian: '0',
        fenXingBian: '0',
        tongGZ: false,
        zuiHouShiMoJian: '0',
        ckd: '0',
        guangJian: '0'
      },
      filterText: '',
      tree_loading: false,
      item_order_id: '2',
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      wenliData:[],
      select_part: [],
      upload:"",
      data3: [],
      nowTab: 1,
      username: '',
      name: '',
      uid: '',
      imageUrls: [],
      restaurants1: [],
      restaurants2: [],
      restaurants3: [],
      restaurants4: [],
      imageData: {
        userId: '',
        imgPakage: 'order_pm'
      },
      ruleForm: {
        orderName: '',
        addUserId: '',
        addUserName: '',
        projectId: '',
        hasRemark: false,
        picUrls: '',
        urgency: '0',
        remark: '',
        realityArea: '0',
        machiningType: ''
      },
      restaurants: [],
      rules: {
        orderName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 0  255 ',
            trigger: 'blur'
          }
        ],
        machiningType: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        managerId: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        realityArea: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        projectId: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        urgency: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],

      }
    }
  },
  components: {
    VueCoreImageUpload
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  },
  methods: {
    addWenLi(){
      let times = this.elform2.times
      let area = this.elform2.area
      if (times=='undefined' || times <=0) {
        this.$message.error("")
        return;
      }
      if (area=='undefined' || area<=0) {
        this.$message.error("")
        return;
      }
      this.elform2.orderId = this.urlId;
      this.jsonPostRequest("/order/list/add/wenli",this.elform2).then(resp => {
        this.innerVisible2 = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.getOrderInfo(this.urlId);
        } else {
          this.$message.error("");
        }
      })

    },
    chanelAdd(){
      this.innerVisible2 = false;
    },
    toEidtor(itemWenLi){
      this.elform1.id = itemWenLi.id;
      this.elform1.orderId = itemWenLi.orderId;
      this.elform1.times = itemWenLi.times;
      this.elform1.area =itemWenLi.area;
      this.elform1.wenliName = itemWenLi.wenliName;
      this.innerVisible1 = true;
    },
    toUpdateWenLi(){
      let data1 = this.elform1;
      let times = data1.times
      let area = data1.area
      if (times=='undefined' || times <=0) {
        this.$message.error("")
        return;
      }
      if (area=='undefined' || area<=0) {
        this.$message.error("")
        return;
      }
      this.jsonPostRequest("/order/list/update/wenli",data1).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.chanel();
          this.getOrderInfo(this.urlId);
        } else {
          this.$message.error("");
        }
      })
    },
    chanel(){
      this.elform1 = {
        wenliName:'',
        area:'0',
        times:'1'
      }
      this.innerVisible1 = false;
    },
    toAddWenLi(){
      this.elform2.wenliName='';
      this.elform2.area='0';
      this.elform2.times='1';
      this.innerVisible2 = true;

    },
    deleteRow(item) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.toDeleteRow(item);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    toDeleteRow(item) {
      this.jsonPostRequest("/order/list/del/wenli", item).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.getOrderInfo(this.urlId);
        } else {
          this.$message.error("");
        }
      })
     },
    changeUrgency1(){
      this.urgencyDisable = true;
    },
    toChangeUrgency(){
      let data = {
        orderId: this.urlId,
        urgency:this.changeUrgency.urgency
      }
      this.postRequest("/order/list/change/urgency", data).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.urgencyDisable = false;
          this.getOrderInfo(this.urlId);
        } else {
          this.$message.error("");
        }
      })
    },

    toUpdateSetMouldPart() {
      let self = this
      let data = {
        orderId: this.urlId,
        uid: this.uid,
        mouldIds: ''
      }
      let mouldIds = '';
      for (let i = 0; i < self.select_part.length; i++) {
        if (i == self.select_part.length - 1) {
          mouldIds = mouldIds + self.select_part[i].collectPartinfoEntity.id
        } else {
          mouldIds = mouldIds + self.select_part[i].collectPartinfoEntity.id + ','
        }
      }
      data.mouldIds = mouldIds

      this.postRequest("/order/list/set/mould", data).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.info("");
          self.getMouldTree();
        } else {
          self.$message.error("");
        }
      })
    },
    delMould(id){
      let params = {
        mouldId:id
      }
      this.postRequest("/order/list/update/set/mould", params).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success('');
          let item = {
            id:id
          }
          this.delSelected(item);
          this.getMouldTree();
        } else {
          this.$message.error('');
        }
      })
    },
    toDeleted(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delMould(id);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    toUpBack(){
      this.$router.push('/order/list')
    },
    toCommit() {
      this.contr_loading = true;
      this.toAddBaseInfo();
    },
    toUpdateBaseInfo(formName) {
      let self = this;
      let url = "";
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let paramsData = self.ruleForm;
          for (var i = 0; i < this.restaurants4.length; i++) {
            if(this.restaurants4[i].value === paramsData.managerId){
              paramsData.managerName = this.restaurants4[i].label
            }
          }
          if (self.imageUrls.length != 0) {
            for (var i = 0; i < self.imageUrls.length; i++) {
              if (i == self.imageUrls.length - 1) {
                url = url + self.imageUrls[i]
              } else {
                url = self.imageUrls[i] + "|" + url
              }
            }
          }
          paramsData.picUrls = url
          paramsData.wenlis = this.wenliData
          this.jsonPostRequest("/order/list/update/order", paramsData).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success('');
            } else {
              self.$message.error('');
            }
          })
        } else {
          self.$message.error('');
          return false;
        }
      });
    },
    toUpdate(){
      let self = this
      this.postRequest("/order/list/update/control", self.exform).then(resp => {
        this.contr_loading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success('');
        } else {
          self.$message.error("");
        }
      })
    },
    toControl() {
      let self = this
      if (self.select_part === "undefined" || !self.select_part.length > 0) {
        self.$message.info("")
        return
      }
      self.nowTab = 3;
    },
    addcontroInfo() {
      let self = this
      self.exform.orderId = self.item_order_id
      self.exform.addUserId = self.uid;
      self.exform.addUserName = self.name;
      this.postRequest("/order/list/add/control", self.exform).then(resp => {
        this.contr_loading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success('');
          self.$store.state.stomp.send("/ws/nf", {}, '');
          //
          this.$router.push('/order/list')
        } else {
          self.$message.error(data.msg);
        }
      })
    },
    initSysMsgs() {
      var _this = this;
      this.getRequest("/chat/sysmsgs").then(resp => {
        _this.sysmsgs = resp.data;
        var isDot = false;
        _this.sysmsgs.forEach(msg => {
          if (msg.state == 0) {
            isDot = true;
          }
        })
        _this.$store.commit('toggleNFDot', isDot);
      })
    },
    splitUrl(item) {
      let picurl = item.picUrls;
      let urlArray = [];
      if (picurl != "undefined" && picurl != "") {
        urlArray = picurl.split('|');
      }
      return urlArray;
    },
    addSelected(item) {
      let self = this
      let sel = self.select_part
      if (sel.length === 0) {
        if (item.collectPartinfoEntity.picurl instanceof Array) {
          sel.push(item);
        } else {
          item.collectPartinfoEntity.picurl = self.splitUrl(item.collectPartinfoEntity)
          sel.push(item);
        }
      } else {
        let flag = true
        for (let i = 0; i < sel.length; i++) {
          if (sel[i].collectPartinfoEntity.id === item.collectPartinfoEntity.id) {
            flag = false;
            break;
          }
        }
        if (flag) {
          if (item.collectPartinfoEntity.picurl instanceof Array) {
            sel.push(item);
          } else {
            item.collectPartinfoEntity.picurl = self.splitUrl(item.collectPartinfoEntity)
            sel.push(item);
          }
        }
      }
    },
    delSelected(item) {
      let self = this
      let sel = self.select_part
      let newS = [];
      for (let i = 0; i < sel.length; i++) {
        if (sel[i].collectPartinfoEntity.id+'' === item.id +'') {
          continue;
        } else {
          newS.push(sel[i]);
        }
      }
      self.select_part = newS;
    },
    toChangeCheck(node, isChecked) {
      let disabled = node.disabled
      if (isChecked && !disabled) { // 
        if (node.collectPartinfoEntity !== "undefined") {
          this.addSelected(node);
        }
      } else if (!isChecked && !disabled) { // 
        if (node.collectPartinfoEntity !== "undefined") {
          this.delSelected(node);
        }
      }
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    toChangeSet() {
      let self = this
      if (self.select_part === "undefined" || !self.select_part.length > 0) {
        self.$message.info("")
        return
      }
      let data = {
        partIds: [],
      }
      for (let i = 0; i < self.select_part.length; i++) {
        data.partIds.push(self.select_part[i].collectPartinfoEntity.id);
      }
      if (data.partIds === "undefined" || !data.partIds.length > 0) {
        self.$message.info("")
        return
      }
      let dataString = JSON.stringify(data)
      let params = {
        data: dataString,
        userId: '123',
        userToken: '',
        md5: '',
        source: ''
      }
      JLApiUtils.updateSetOrderMould(params).then(function(data) {
        if (data.code == '0') {
          self.$message.success('');
          self.getMouldTree();
          self.select_part = [];
          self.is_set = false;
        } else {
          self.$message.error(data.msg);
        }
      })
    },
    toSetMouldPart() {
      let self = this
      let data = {
        orderId: self.item_order_id,
        uid: this.uid,
        mouldIds: ''
      }
      let mouldIds = '';
      for (let i = 0; i < self.select_part.length; i++) {
        if (i == self.select_part.length - 1) {
          mouldIds = mouldIds + self.select_part[i].collectPartinfoEntity.id
        } else {
          mouldIds = mouldIds + self.select_part[i].collectPartinfoEntity.id + ','
        }
      }
      data.mouldIds = mouldIds
      this.postRequest("/order/list/set/mould", data).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          // 
          this.addcontroInfo();
        } else {
          self.$message.error("");
        }
      })
    },
    getMouldTree() {
      let self = this
      this.getRequest("/order/list/mould/tree").then(resp => {
        this.tree_loading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.data3 = resp.data.data.tree;
        }
      })
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    resetForm1(formName) {
      this.exform = {
        addUserId: '',
        addUserName: '',
        orderId: '',
        qiKong: '',
        queKou: '',
        lieFeng: '',
        hanBa: '',
        dianHuoHua: '',
        youShiSi: '',
        heMoBian: '0',
        fenXingBian: '0',
        tongGZ: false,
        zuiHouShiMoJian: '0',
        ckd: '0',
        guangJian: '0'
      }
    },

    handleSelect(item) {
      console.log(item);
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    querySearch1(queryString, cb) {
      var restaurants = this.restaurants1;
      var results = queryString ? restaurants1.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    querySearch2(queryString, cb) {
      var restaurants = this.restaurants2;
      var results = queryString ? restaurants2.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    querySearch5(queryString, cb) {
      var restaurants = this.restaurants5;
      var results = queryString ? restaurants5.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    querySearch6(queryString, cb) {
      var restaurants = this.restaurants6;
      var results = queryString ? restaurants6.filter(this.createFilter(queryString)) : restaurants;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    delMouldImage(url) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.todelImage(url);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    todelImage(url) {
      let self = this
      if (url == 'undefined' || url.length == "") {
        self.$message.error("");
        return
      }
      this.getRequest("/config/delfile/delImage?url=" + url).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          //self.$message.success("");
          let urls = self.imageUrls;
          let newUrls = [];
          for (var i = 0; i < urls.length; i++) {
            if (url != urls[i]) {
              newUrls.push(urls[i])
            }
          }
          self.imageUrls = newUrls;
          this.toUpdateBaseInfo('ruleForm')
        } else {
          self.$message.error("");
        }
      })

    },
    imageuploaded(data) {
      let self = this;
      if (data.url.length !== 'undefined' && data.url.length > 0) {
        let url = data.url
        let urls = self.imageUrls;
        for (var i = 0; i < url.length; i++) {
          urls.push(url[i]);
        }
        self.imageUrls = urls;
      }
    },
    // 
    handleError() {
      this.$notify.error({
        title: '',
        message: ''
      });
    },
    loadAllProject() {
      let self = this
      this.getRequest("/project/list/byuid?uid=" + this.uid).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let re = resp.data.data.projectlist;
          if (re != 'undefined' && re.length > 0) {
            for (var i = 0; i < re.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = re[i].projectName
              elem.value = re[i].id
              item.push(elem);
            }
            self.restaurants3 = item;
          }
        } else {
          console.log("");
        }
      });
    },
    loadAllManger() {
      let self = this
      this.getRequest("/order/list/mangers?role=ROLE_chejian").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let item = []
          let re = resp.data.data;
          if (re != 'undefined' && re.length > 0) {
            for (var i = 0; i < re.length; i++) {
              let elem = {
                label: '',
                value: ''
              }
              elem.label = re[i].name
              elem.value = re[i].id
              item.push(elem);
            }
            self.restaurants4 = item;
          }
        } else {
          console.log("");
        }
      });
    },
    getOrderInfo(urlId){
      let self = this;
      this.getRequest("/order/list/info?orderId=" + urlId).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.ruleForm = resp.data.data.orderinfo;
          // console.log(resp.data.data.orderinfo);
          this.planStatus = resp.data.data.orderinfo.plantStatus;
          this.ruleForm.urgency = this.ruleForm.urgency+'';
          if('undefined' != resp.data.data.orderinfo.picUrls
          && resp.data.data.orderinfo.picUrls.length>0){
            var te = this.$options.filters['splitUrl'];
            let urls = te(resp.data.data.orderinfo.picUrls)
            this.imageUrls = urls
          }
          this.qrCode = resp.data.data.orderinfo.qrCode;
          this.exform = resp.data.data.orderinfo.controlOrderFrom;
          let moulds = resp.data.data.orderinfo.mouldInfoList;
          this.wenliData = this.ruleForm.wenlis
          if('undefined' != moulds && moulds.length > 0){
            for(let i= 0;i< moulds.length;i++){
              let part = {
                collectPartinfoEntity:{}
              }
              part.collectPartinfoEntity = moulds[i];
              this.addSelected(part);
            }
          }
        } else {
          console.log("");
        }
      });
    },
    // 
    handleError() {
      this.$notify.error({
        title: '',
        message: ''
      });
    },
    loadAll5() {
      return [{
          "value": ""
        },
        {
          "value": ""
        }
      ];
    },
    loadAll6() {
      return [{
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        }
      ];
    },
    loadAll() {
      return [{
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        }
      ];
    },
    loadAll1() {
      return [{
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        }
      ];
    },
    loadAll2() {
      return [{
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        },
        {
          "value": ""
        }
      ];
    },
    // 
    getCollectMouldList() {
      let _this = this
      this.getRequest("/order/list/listbypage?page=" + this.currentPage + "&size=" + this.pagesize + "&addUserId=" + this.uid).then(resp => {
        _this.tableLoading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          _this.tableData = resp.data.data.orderlist
          _this.totalnum = resp.data.data.count
        }
      })
    }
  },
  computed: {
    isShangWu: function () {
      var roles = this.$store.state.user.roles;
      var isShangWu = false;
      roles.forEach(role=> {
        if (role.name == 'ROLE_admin' || role.name == 'ROLE_shangwu') {
          isShangWu = true;
        }
      })
      return isShangWu;
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.imageData.userId = localStorage.getItem('cp_uid');
      this.upload = JLApiUtils.upload;
    })
    this.restaurants = this.loadAll();
    this.restaurants1 = this.loadAll1();
    this.restaurants2 = this.loadAll2();
    this.restaurants5 = this.loadAll5();
    this.restaurants6 = this.loadAll6();
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.restaurants = this.loadAll();
    this.loadAllProject();
    this.loadAllManger();
    this.tree_loading = true;
    this.getMouldTree();
  },
  created: function() {
    this.urlId = this.$route.params.id;
    this.getOrderInfo(this.urlId);
  }
}
</script>
<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.el-select .el-input {
  width: 130px;
}

.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



